import React from 'react'
import Button from 'react-bootstrap/Button';
import Feature from './Feature';
import PriceItem from './PriceItem';
import SubModule from './SubModule';
import {actions} from "../../core/store";
import {connect} from "unistore/react";

class Module extends React.Component {

    constructor(props) {
        super(props);
        this.state = {expanded: true};
    }

    addToCart = (priceItem, price, action) => {
        if (action === 'add') {
            this.props.addToCart(priceItem, price)
        }
        else if (action === 'remove') {
            this.props.removeFromCart(priceItem)
        }
        else {
            console.log('unknown action');
        }
    }

    render() {
        return (
            <div className="card-container">
                <div className="module-head row align-items-center">
                    <div className="col-12 col-md-9">
                        <div className="card-icon" title={this.props.data.code}>
                            {this.props.data.code}
                        </div>
                        <div className="card-summary-container">
                            <h4 className="card-title">
                                {this.props.data.name}
                            </h4>
                            <span className="card-desc">
                {this.props.data.description}
              </span>
                        </div>

                        <div className="card-images">
                            <div className="card-image">XY</div>
                            <div className="card-image">XY</div>
                        </div>

                    </div>
                    <PriceItem
                        className="col-12 col-md-3"
                        code={this.props.code}
                        name={this.props.data.name}
                        app={this.props.app}
                        group={this.props.group}
                        data={this.props.data}
                        multipliers={this.props.multipliers}
                    >

                        <Button variant="outline-info" className="ml-2"
                                onClick={() => this.setState({expanded: !this.state.expanded})}>{this.state.expanded ? '-' : '+'}</Button>
                    </PriceItem>

                </div>

                <div className={this.state.expanded ? 'card-features' : 'card-features d-none'}>
                    {this.props.data.submodules.map((submodule, index) => <SubModule key={index} data={submodule}/>)}
                </div>

                <div className={this.state.expanded ? 'card-features' : 'card-features d-none'}>
                    {this.props.data.features.map((feature, index) => <Feature key={index} data={feature}/>)}
                </div>
            </div>
        )
    }
}

export default connect([], actions)(Module);