import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Provider } from 'unistore/react'
import { store, actions } from './core/store'
import events from './core/events';


const map_actions = (actions, store) => {
	if (typeof actions==='function') actions = actions(store);
	let mapped = {};
	for (let i in actions) {
		mapped[i] = store.action(actions[i]);
	}
	return mapped;
}


export default class Pricing {

    constructor(elem, initState, addToCartCallback) {
        this.store = store;
        this.actions = map_actions(actions, store);
        this.addToCartCallback = addToCartCallback;

        this.actions.initialize(initState);
        ReactDOM.render((
            <Provider store={this.store}>
                <App />
            </Provider>
        ), elem);

        this.setUpListeners();
    }

    setUpListeners() {
        document.addEventListener(events.ADD_TO_CART_EVENT, (e) => {
            if (typeof this.addToCartCallback === 'function') {
                this.addToCartCallback(this.store.getState().cart)
            }
        })
    }
}
