import createStore from 'unistore'
import {SOFTWARE} from "./ItemConfig";
import calculate_price from './PricingCalc';


let store = createStore({
    configData: [],
    global: {
        'domain': 'www.divineit.net',
        'customerRef': 'Divine IT Limited - ZubaiR'
    },
    params: {
        'params.users': 1,
        'params.accounts': 500,
        'params.branches': 4
    },
    cartItems: [],
    cartSubItems: [],
    modules:[],
    PricingParameters: [],

    cart: {}
})


const update_cart_wrapper = (fnc) => {
    return (...args) => {
        const new_state = fnc(...args)
        return {...new_state, ...{ cart: calculate_price(new_state.cartItems, new_state.configData, new_state.params)}}
    }
}

const wrap_actions = (actions, wrapper) => {
    let wrapped_actions = {}
    for (let key in actions){
       if(actions.hasOwnProperty(key)){
           wrapped_actions[key] = wrapper(actions[key])
       }
    }
    return wrapped_actions;
}

const cart_related_actions = {

    initialize(state, initState) {
        return { ...state, ...initState }
    },

    addToCart(state, priceItem, price) {
        console.log('adding item', priceItem.code);
        const newItem = {
            type: SOFTWARE,
            code: priceItem.code,
            name: priceItem.name,
            app: priceItem.app,
            parameter1: null,
            unitPrice: null,
            freeUnit: 0,
            freeprice: 0.0,
            price: price
        }
        return {...state, ...{cartItems: [...state.cartItems, newItem]}}
    },

    removeFromCart(state, priceItem) {
       return {...state, ...{cartItems: state.cartItems.filter(cartItem => cartItem.code !== priceItem.code)}}
    },

    updateParam(state, name, value) {
        return {...state, ...{ params: {...state.params, [name]: value} }}
    }
}


let other_actions = {
    // actions that does not need to update cart
}

let actions = store => ({...wrap_actions(cart_related_actions, update_cart_wrapper), ...other_actions})

export {
    store,
    actions
}