import React from 'react'
import PricingParameterPrimary from './PricingParameterPrimary';
import {actions} from "../../core/store";
import {connect} from "unistore/react";


const MultiplierInputByCode = ({multipliers, code, ...props}) => {
  return (
      <div className="row">
      {
        <PricingParameterPrimary
          parent={'params.' + multipliers[code].code}
          name={ multipliers[code].name}
          label={multipliers[code].label}
          valueSource={multipliers[code].sources}
          slabs={multipliers[code].slabs}
          {...props}
        />
      }
    </div>
  )
}


export default connect([], actions)(MultiplierInputByCode);