    //     // if (unitmod < 5) unitmod = 0;
    //     // else unitmod = 5;

    //     newCartItems[index]['freeUnit'] = (unit10 + unitmod) / 10.0
    //     console.log('updating', price);
    //   }
    //   else {
    //     newCartItems.push({
    //       type: each,
