import React from 'react';

import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import AppTab from '../AppTab';
import Cart from '../Cart/Cart';
import {connect} from 'unistore/react';
import {actions} from '../../core/store';
import Parameters from '../Param/Param';

class CompositePricingEngine extends React.Component {

    render() {
        return (
            <div className="row">
                <div className="col-9">

                    <Parameters />

                    <Tabs defaultActiveKey={this.props.configData[0].code} id="uncontrolled-tab-example">
                        {this.props.configData.map(app => (
                            <Tab key={app.code} eventKey={app.code} title={app.code}>
                                <AppTab key={app.code} code={app.code} data={app} />
                            </Tab>
                        ))}
                    </Tabs>
                </div>

                <div className="col-3">
                    <Cart />
                </div>
            </div>
        );
    }
}

export default connect(['configData'], actions)(CompositePricingEngine);
