import React from 'react';
import Button from 'react-bootstrap/Button';
import Feature from './Feature';

class SubModule extends React.Component {

  render() {
    return (
      <div className="submodule row">
        <div className="col-12 col-md-9">
          <div className="feature-title">
            <div className="title">{this.props.data.name}</div>
            <div className="card-desc">{this.props.data.name}</div>
          </div>

          <div className="card-images">
            <div className="card-image">XY</div>
            <div className="card-image">XY</div>
          </div>

        </div>

        <div className="col-12 col-md-3 price-container">
          <div className="status-container">
            <Button className="primary">Add</Button>
          </div>

          <div className="price-text">25 * 7.99</div>
          <div className="price">
              {this.props.data.price && this.props.data.price.slab1 ? this.props.data.price.slab1 / 1000 : ''},
              <span>{this.props.data.price && this.props.data.price.slab1 ? (this.props.data.price.slab1 % 1000).toString().padStart(3, "0") : ''}</span>
          </div>
        </div>

        <div className="col-12 card-features">
        { this.props.data.features.map((feature, index) => <Feature key={index} data={feature} />) }
        </div>

      </div>
    )
  }
}

export default SubModule;