import React from 'react'
import Button from 'react-bootstrap/Button'
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import {actions} from "../../core/store";
import {connect} from "unistore/react";
import PricingParameter, { PricingParameterBase } from "./PricingParameter";
import Col from "react-bootstrap/Col";

class PricingParameterPrimary extends PricingParameterBase {
  render() {
    // for (var each in this.props.valueSources) {
    //   console.log('value source found');
    // }
    const { paramValue } = {...this.state};
    let {slabs} = {...this.props};
    if (undefined === slabs) slabs = [];

    return (
      <div className="col-6">

        <Form.Group as={Col} controlId="formGridEmail">
          <Form.Label>{this.props.label}</Form.Label>

          <InputGroup>
            <InputGroup.Prepend>
              <ButtonGroup size="" className="" aria-label="First group">
                { slabs.map((slab,index) =>
                  <Button variant={ this.state.selectedSlabIndex == index ? 'primary' : 'secondary' } onClick={() => this.handleSlabButtonClick(this.props.name, slab, index)} key={index} value={slab}>{slabs[index-1] + 1} - {slab}</Button>
                )}
              <Button variant={ this.state.selectedCustomSlab ? 'primary' : 'secondary' }
                onClick={() => this.handleSlabButtonClick(this.props.name, null, null)}
              >Custom 1</Button>
              </ButtonGroup>
            </InputGroup.Prepend>
            <Form.Control
              type="text"
              placeholder={this.props.label}
              aria-describedby="inputGroupPrepend"
              required
              name={this.props.name} onChange={this.handleInputChange} value={paramValue}
            />
            <Form.Control.Feedback type="invalid">
              Please provide a valid input
            </Form.Control.Feedback>
          </InputGroup>

        </Form.Group>
      </div>
    )
  }
}


export default connect(['params'], actions)(PricingParameterPrimary);